import { FC } from "react"

interface IProps {
    children: any
}

const CopyableText: FC<IProps> = (props) => {
    const text = props.children
    const style = { cursor: "pointer", color: "var(--color-05)" }

    return (
        <span style={style} title="Clique para copiar o texto!" onClick={(event: any) => {
            navigator.clipboard.writeText(text)
            event.target.innerText = "Copiado para a rea de transferncia!"
            
            setTimeout(() => {
                event.target.innerText = text
            }, 1500)
        }}>{text}</span>
    )
}

export default CopyableText