import { FC, useEffect, useRef } from "react"

const AdCashBanner: FC = () => {
    const adsHtml = window.screen.width > 900 ? `<script data-cfasync="false" type="text/javascript" src="//brightonclick.com/a/display.php?r=7123362"></script><style>*{padding: 0;margin: 0;box-sizing: border-box;}</style>` : `<script data-cfasync="false" type="text/javascript" src="//brightonclick.com/a/display.php?r=7123358"></script><style>*{padding: 0;margin: 0;box-sizing: border-box;}</style>`

    const iframeRef = useRef(null)
    let style

    if (window.screen.width > 900) {
        style = {
            maxWidth: "728px",
            width: "100%"
        }
    }

    useEffect(() => {
        const iframe = iframeRef.current as any;
        const iframeDocument = iframe.contentDocument || iframe.contentWindow.document
        iframeDocument.open()
        iframeDocument.write(adsHtml)
        iframeDocument.close()
    }, [])

    return (
        <div className="mt-3 mb-3 d-flex justify-content-center animate-load-from-bottom">
            <iframe
                width={window.screen.width > 900 ? 728 : 300}
                height={window.screen.width > 900 ? 90 : 250}
                ref={iframeRef}
                frameBorder={0}
                style={style}
            />
        </div>
    )
}

export default AdCashBanner