import { Container, Alert } from "react-bootstrap"
import { Link } from "react-router-dom"
import { Helmet } from "react-helmet"
import { FC, useState } from "react"

import AnonymusAdsBanner from "../../components/anonymus-ads-banner"
import AdsterraBanner from "../../components/adsterra-banner"
import VideoKeywords from "../../components/video-keywords"
import AdCashBanner from "../../components/adcash-banner"
import Footer from "../../components/footer"
import Header from "../../components/header"

import videoTutorialAmicoImage from "../../assets/images/ilustrations/Video tutorial-amico.svg"
import videoUploadAmicoImage from "../../assets/images/ilustrations/Video upload-amico.svg"
import seoAnalyticsTeamAmicoImage from "../../assets/images/ilustrations/SEO analytics team-amico.svg"
import webSearchAmicoImage from "../../assets/images/ilustrations/Web search-amico.svg"

import "./style.css"

function isValidUrl(url: string): boolean {
    const patten = /^(http[s]?:\/\/)?(www\.)?(youtube\.com|youtu\.be)/
    return patten.test(url)
}

const Home: FC = () => {
    const [ alert, setAlert ] = useState<any>()
    const [ videoKeywordsElement, setVideoKeywordsElement ] = useState<any>()

    function toggleScrollToTopButton() {
        const $btnScrollToTop = document.getElementById("btnScrollToTop")

        if ($btnScrollToTop) {
            if (document.documentElement.scrollTop > 300) {
                $btnScrollToTop.style.visibility = "visible"
                $btnScrollToTop.style.opacity = "1"
            } else {
                $btnScrollToTop.style.visibility = "hidden"
                $btnScrollToTop.style.opacity = "0"
            }
        }
    }
      
    function scrollToTop() {
        window.scrollTo({
            top: 0,
            behavior: "smooth"
        })
    }
      
    window.onscroll = () => toggleScrollToTopButton()

    function handleGetInfo(event: any) {
        setVideoKeywordsElement(null)

        const $videoUrlInput = document.querySelector("#video-url") as any
        const videoUrl = $videoUrlInput.value as string

        if (!isValidUrl(videoUrl)) {
            setTimeout(setAlert, 1500)

            return setAlert(
                <Alert variant="danger">Insira uma URL vlida!</Alert>
            )
        }

        $videoUrlInput.disabled = true

        setAlert(
            <Alert variant="primary">Aguarde estamos buscando as tags do vdeo...</Alert>
        )

        fetch(`https://marcuth-utils-api.marcuth.repl.co/scraping/youtube/video/details/?url=${videoUrl}`)
            .then(response => response.json())
            .then(videoInfo => {
                setVideoKeywordsElement(
                    <VideoKeywords keywords={videoInfo.keywords}/>
                )

                $videoUrlInput.disabled = false

                setAlert(
                    <Alert variant="success">As tags foram obtidas com sucesso!</Alert>
                )

                setTimeout(() => setAlert(null), 1500)
            })
            .catch(error => {
                setTimeout(setAlert, 1500)

                return setAlert(
                    <Alert variant="danger">{error.message}</Alert>
                )
            })
    }

    return (
        <>
            <Helmet>
                <title>yTags - Busque Tags e Palavras-Chave de Vdeos do YouTube | Home</title>
            </Helmet>
            <Header/>
            <Container>
                <h1 className="main-title animate-load-from-top">Seja bem-vindo ao <strong>yTags!</strong></h1>
                <div className="url-form animate-load-from-left">
                    <h2 className="secondary-title mb-3">Pegue tags de qualquer vdeo do YouTube e melhore o SEO do seu Canal!</h2>
                    <div className="fields">
                        <input id="video-url" placeholder="https://youtu.be/5u_-eo1bXec" type="url"/>
                        <button className="btn-send" title="Pegar tags do vdeo" onClick={handleGetInfo}>
                            <i className="bi bi-send"></i>
                        </button>
                    </div>
                    <div className="mt-3">{alert}</div>
                </div>
                {videoKeywordsElement}
                <p className="text-center mt-3 mb-3 animate-load-from-bottom">Anncio</p>
                <AnonymusAdsBanner/>
                <h4 className="text-center mt-4 animate-load-from-bottom" onClick={(event) => {
                    window.alert("Opa! Perdo por isso, mas nosso site ainda est em construo e no fizemos um guia prtico de como usar nossa ferramenta, mas no desista de ns,  bem fcil de usar: basta copiar o link do vdeo que voc quer descobir as tags no YouTube e colar no nosso caompo de URL. Feito isso, clique no aviozinho e aguarde um instante. Aps isso aparecer todas as tags daquele vdeo :D")
                }}><Link className="text-decoration-none" to="/#">No sabe como usar? Clique aqui!</Link></h4>
                <img className="main-image animate-load-from-bottom" src={videoTutorialAmicoImage} alt="Video tutorial"/>
                <p className="animate-load-from-bottom">Bem-vindo(a) ao yTag, o seu destino definitivo para descobrir as tags de qualquer vdeo do YouTube! Estamos empolgados por t-lo(a) aqui, onde voc pode explorar e encontrar facilmente as tags que impulsionam os vdeos de sucesso. Sabemos que criar contedo no YouTube  emocionante, mas tambm pode ser desafiador alcanar um pblico amplo e obter visibilidade.  a que entra o SEO (Search Engine Optimization) para o YouTube, uma estratgia crucial para aumentar a visibilidade e o alcance do seu vdeo nos mecanismos de busca.</p>
                <p className="animate-load-from-bottom">As tags so componentes essenciais do SEO para o YouTube. Elas so palavras-chave ou frases curtas que descrevem o contedo do seu vdeo e ajudam a categoriz-lo. Ao adicionar as tags certas, voc aumenta suas chances de ser encontrado por usurios interessados no seu tpico. E aqui no yTag, voc ter acesso s tags mais relevantes e populares usadas por criadores de contedo de sucesso.</p>
                <p className="animate-load-from-bottom">Ao analisar as tags utilizadas por criadores bem-sucedidos, voc pode aprender com eles e aplicar estratgias semelhantes em seus prprios vdeos. Nossa plataforma torna fcil descobrir as tags usadas nos vdeos mais populares, permitindo que voc otimize as tags do seu prprio vdeo para aumentar sua visibilidade nas pesquisas do YouTube.</p>
                <p className="animate-load-from-bottom">Estamos aqui para ajud-lo(a) a aprimorar sua experincia no YouTube e fornecer as ferramentas necessrias para impulsionar seu canal. Com o poder do SEO para o YouTube, voc pode maximizar o potencial do seu contedo e alcanar resultados incrveis. Ento, no perca mais tempo! Junte-se a ns no yTag e vamos comear a explorar as tags que levaro seu contedo a novos patamares. Estamos prontos para transformar suas ideias em sucesso no YouTube!</p>
                <hr className="animate-load-from-bottom"/>
                <h2 className="animate-load-from-bottom">O que so tags em um vdeo?</h2>
                <p className="text-center mt-3 mb-3 animate-load-from-bottom">Anncio</p>
                <AdsterraBanner/>
                <img className="animate-load-from-bottom" src={videoUploadAmicoImage} alt="Upload de vdeo"/>
                <p className="animate-load-from-bottom">As tags no YouTube so palavras-chave ou frases curtas que descrevem o contedo de um vdeo. Elas servem como metadados que ajudam a categorizar e identificar o assunto do vdeo. As tags so adicionadas pelos criadores de contedo durante o processo de upload e desempenham um papel importante na otimizao e descoberta de vdeos.</p>
                <p className="animate-load-from-bottom">Ao adicionar tags relevantes ao seu vdeo, voc facilita a pesquisa e a localizao do seu contedo por parte dos usurios. As tags podem abranger termos gerais relacionados ao tpico principal do vdeo, bem como palavras-chave especficas que descrevem detalhes importantes. Ao escolher tags adequadas e precisas, voc aumenta as chances de seu vdeo ser encontrado por usurios interessados no seu contedo.</p>
                <p className="animate-load-from-bottom">No entanto,  importante equilibrar a relevncia e a popularidade das tags. As tags relevantes devem estar diretamente relacionadas ao contedo do seu vdeo, enquanto as tags populares so aquelas que so frequentemente pesquisadas pelos usurios. Encontrar a combinao certa de tags relevantes e populares pode aumentar a visibilidade do seu vdeo e atrair mais espectadores.</p>
                <hr className="animate-load-from-bottom"/>
                <h2 className="animate-load-from-bottom">O que  SEO?</h2>
                <p className="text-center mt-3 mb-3 animate-load-from-bottom">Anncio</p>
                <AdsterraBanner/>
                <img className="animate-load-from-bottom" src={seoAnalyticsTeamAmicoImage} alt="Equipe de anlise de SEO"/>
                <p className="animate-load-from-bottom">SEO (Search Engine Optimization), ou otimizao para mecanismos de busca,  uma estratgia utilizada para melhorar a visibilidade e a classificao de um contedo nos resultados dos mecanismos de busca, como o YouTube. O objetivo do SEO  atrair trfego orgnico e direcionar usurios interessados para o seu contedo.</p>
                <p className="animate-load-from-bottom">No contexto do YouTube, o SEO envolve a otimizao de diferentes elementos do seu vdeo, incluindo as tags, a descrio, o ttulo, as miniaturas e as legendas. Ao otimizar esses elementos, voc aumenta suas chances de ser encontrado e recomendado pelo algoritmo do YouTube.</p>
                <p className="animate-load-from-bottom">Alm das tags, o SEO tambm leva em considerao fatores como a qualidade do contedo, o engajamento do pblico, a reteno de espectadores, o nmero de visualizaes e curtidas, entre outros. A otimizao adequada do seu vdeo e do seu canal pode levar a um aumento no trfego, nas visualizaes e no crescimento do pblico.</p>
                <hr className="animate-load-from-bottom"/>
                <h2 className="animate-load-from-bottom">Como o SEO afeta meus vdeos?</h2>
                <p className="text-center mt-3 mb-3 animate-load-from-bottom">Anncio</p>
                <AnonymusAdsBanner/>
                <img className="animate-load-from-bottom" src={webSearchAmicoImage} alt="Pesquisa web"/>
                <p className="animate-load-from-bottom">O SEO desempenha um papel significativo no desempenho dos seus vdeos no YouTube. Ao aplicar estratgias de SEO eficazes, voc pode afetar positivamente a visibilidade e o alcance do seu contedo, influenciando diretamente o nmero de visualizaes e o engajamento dos espectadores.</p>
                <p className="animate-load-from-bottom">Uma boa otimizao de SEO, incluindo o uso adequado de tags relevantes e populares, torna mais provvel que o algoritmo do YouTube exiba seu vdeo nos resultados de pesquisa relevantes. Isso aumenta a exposio do seu contedo a um pblico mais amplo e direcionado.</p>
                <p className="animate-load-from-bottom">Alm disso, um vdeo otimizado para SEO tem mais chances de ser recomendado pelo algoritmo do YouTube em sees como "Vdeos em destaque" e "Vdeos relacionados". Essas recomendaes podem gerar um aumento significativo no nmero de visualizaes e, potencialmente, atrair mais inscritos para o seu canal.</p>
                <p className="animate-load-from-bottom">No entanto,  importante lembrar que o SEO  apenas uma parte do sucesso no YouTube. A qualidade do contedo, a consistncia na publicao de vdeos, a interao com o pblico e a criao de relacionamentos com outros criadores tambm so aspectos essenciais para o crescimento do seu canal. Ao combinar uma estratgia slida de SEO com contedo valioso e autntico, voc pode maximizar o potencial de sucesso dos seus vdeos no YouTube.</p>
                <hr className="animate-load-from-bottom"/>
                <h2 className="animate-load-from-bottom">Extra!</h2>
                <p className="text-center mt-3 mb-3 animate-load-from-bottom">Anncio</p>
                <AdCashBanner/>
                <p className="animate-load-from-bottom">Se voc tiver interesse em aprofundar seus conhecimentos sobre SEO, recomendamos explorar as seguintes fontes de aprendizado:</p>
                <ul className="animate-load-from-bottom">
                    <li>
                        <p><Link target="_blank" to="https://amzn.to/3D6UwfT">SEO 2023: Aprenda otimizao de mecanismo de pesquisa com estratgias inteligentes de marketing na Internet</Link></p>
                        <p>Este livro aborda estratgias atualizadas de SEO e fornece orientaes prticas para melhorar a classificao nos mecanismos de busca, otimizar o contedo e aumentar a visibilidade online.</p>
                    </li>
                    <li>
                        <p><Link target="_blank" to="https://amzn.to/3O4Yo7k">A Arte do SEO: Dominando a Otimizao para Mecanismos de Busca</Link></p>
                        <p>Considerado um dos principais guias sobre SEO, este livro abrange desde os conceitos bsicos at as estratgias avanadas, oferecendo uma viso abrangente sobre a otimizao para mecanismos de busca.</p>
                    </li>
                    <li>
                        <p><Link target="_blank" to="https://amzn.to/43o97hA">Workbook de SEO: Os Sete Passos para o Sucesso da Otimizao para Mecanismos de Busca no Google</Link></p>
                        <p>Este livro apresenta um formato de workbook, onde voc pode realizar exerccios prticos para aprimorar suas habilidades em SEO. Ele abrange os fundamentos do SEO, anlise de concorrncia, palavras-chave, link building e muito mais.</p>
                    </li>
                    <li>
                        <p><Link target="_blank" to="https://amzn.to/3pzIZmh">Otimizao para Mecanismos de Busca para Leigos - Tudo em Um</Link></p>
                        <p>Um guia abrangente que aborda diversos aspectos do SEO, incluindo palavras-chave, otimizao de contedo, link building, anlise de dados e estratgias para obter melhores resultados nos mecanismos de busca.</p>
                    </li>
                    <li>
                        <p><Link target="_blank" to="https://amzn.to/3ro36nZ">SEO como se eu Tivesse 5 Anos: O Guia Definitivo para Iniciantes em Otimizao para Mecanismos de Busca</Link></p>
                        <p>Um livro voltado para iniciantes que descomplica os conceitos de SEO e oferece orientaes prticas para melhorar a visibilidade do seu site nos mecanismos de busca.</p>
                    </li>
                </ul>
                <p className="animate-load-from-bottom">Lembre-se de que a indstria de SEO est em constante evoluo, portanto, ao ler esses livros, certifique-se de complementar seu conhecimento com pesquisas atualizadas e prticas recomendadas mais recentes.</p>
                <button id="btnScrollToTop" title="Voltar ao topo" onClick={scrollToTop}>
                    <i className="bi bi-chevron-up"></i>
                </button>
            </Container>
            <Footer/>
        </>
    )
}

export default Home