import { Link } from "react-router-dom"
import { FC } from "react"

import "./style.css"

const Footer: FC = () => {
    return (
        <footer className="pg-footer">
            <Link className="logo" to="/">yTags</Link>
            <hr/>
            <div className="comlumns">
                <div className="author-info">
                    <img src="https://avatars.githubusercontent.com/u/91915075?v=4" alt="Site author"/>
                    <span className="name">Marcuth</span>
                </div>
                <ul className="links">
                    <span className="title">Links</span>
                    <li>
                        <Link target="_blank" to="https://github.com/1Marcuth">Perfil do Marcuth no GitHub</Link>
                    </li>
                    <li>
                        <Link target="_blank" to="https://storyset.com/technology">Ilustraes de tecnologia por Storyset</Link>
                    </li>
                    <li>
                        <Link target="_blank" to="https://ytdl.fun/">Baixe vdeos do YouTube com Ytdl.fun!</Link>
                    </li>
                    <li>
                        <Link target="_blank" to="https://kest.fun/">Encurte URLs de graa com Kest.fun!</Link>
                    </li>
                </ul>
            </div>
        </footer>
    )
}

export default Footer