import { FC } from "react"

import CopyableText from "../copyable-text"

import "./style.css"

interface IProps {
    keywords: string[],
}

const VideoKeywords: FC<IProps> = ({ keywords  }) => {
    return (
        <div className="video-keywords animate-load-from-top">
            <h3>Tags do vdeo</h3>
            <p style={{ color: "var(--color-02)" }}  className="small"><b>Dica:</b> clique nas tags para copiar para a rea de transferncia.</p>
            <div className="keywords">
                {keywords.length > 0 ? (
                    <CopyableText>
                        {keywords.join(", ")}
                    </CopyableText>
                ) : (
                    <p>Desculpe, nenhuma tag foi encontrada :/</p>
                )}
            </div>
            <div className="mt-3"></div>
        </div>
    )
}

export default VideoKeywords