import { FC, useEffect, useRef } from "react"

const AdsterraBanner: FC = () => {
    const adsHtml = window.screen.width > 900 ? `<script type="text/javascript">atOptions={'key' : '8b6f8fd74f0a768a65f8f54d532d2c72','format' : 'iframe','height' : 90,'width' : 728,'params' :{}};document.write('<scr' + 'ipt type="text/javascript" src="http' + (location.protocol==='https:' ? 's' : '') + '://www.profitabledisplaynetwork.com/8b6f8fd74f0a768a65f8f54d532d2c72/invoke.js"></scr' + 'ipt>');</script><style>*{padding: 0;margin: 0;box-sizing: border-box;}</style>` : `<script type="text/javascript">atOptions={'key' : '91dff8707e51b06d7556ff323634096d','format' : 'iframe','height' : 50,'width' : 320,'params' :{}};document.write('<scr' + 'ipt type="text/javascript" src="http' + (location.protocol==='https:' ? 's' : '') + '://www.profitabledisplaynetwork.com/91dff8707e51b06d7556ff323634096d/invoke.js"></scr' + 'ipt>');</script><style>*{padding: 0;margin: 0;box-sizing: border-box;}</style>`

    const iframeRef = useRef(null)
    let style

    if (window.screen.width > 900) {
        style = {
            maxWidth: "728px",
            width: "100%"
        }
    }

    useEffect(() => {
        const iframe = iframeRef.current as any;
        const iframeDocument = iframe.contentDocument || iframe.contentWindow.document
        iframeDocument.open()
        iframeDocument.write(adsHtml)
        iframeDocument.close()
    }, [])

    return (
        <div className="mt-3 mb-3 d-flex justify-content-center animate-load-from-bottom">
            <iframe
                width={window.screen.width > 900 ? 728 : 320}
                height={window.screen.width > 900 ? 90 : 50}
                ref={iframeRef}
                frameBorder={0}
                style={style}
            />
        </div>
    )
}

export default AdsterraBanner